@import '../variables/variables';


.movie-thumbnail {
	border-radius: 0.5rem;
	border: 1px solid rgba(white, 0.3);
	align-self: stretch;
	max-height: 28rem;
	min-height: 28rem;
	min-width: 18.33rem;
	max-width: 18.333rem;
	cursor: pointer;

}

img .movie-thumbnail {
	max-height: 2.8rem;
}


.movieHeader {
	color: #fff;
	font-size: 1.5rem;
	font-weight: 400;
	letter-spacing: 0.1rem;
	text-align: center;
	word-wrap: break-word;
	padding-top: 0.5rem;
}

.button-discover {
	color: blue;
	width: 100px;
}

.movie-container {
	margin-bottom: 10rem;

	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	align-items: flex-start;
	min-width: 20rem;
}

.movie-card {
	margin: 7rem;
	max-width: 18.5rem;
	max-height: 23rem;
}

.similarMovies-banner {
	padding-top: 2rem;
}

.similarMovies-header {
	text-align: center;
	position: absolute;
	padding-bottom: 10rem;
	margin-bottom: 10rem;
	width: 100%;
	color: #6495ED;
}

.similar-movie-container {
	width: 100%;
	position: absolute;
	margin-bottom: 10rem;
	margin-top: 3rem;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	min-width: 20rem;
	// z-index: 999;
}

.similar-movie-card {
	margin-top: 1rem;
	margin: 2rem;
	max-width: 18.5rem;
	max-height: 23rem;
	padding: 1rem;
	margin-bottom: 6rem;
}


.movie-thumbnail:hover {
	transform: scale(0.85);
	transition-duration: 0.5s;
}

.pageNumbers:hover {
	transform: scale(1.5);
	transition-duration: 0.1s;
}

.no-movie-text {
	padding-top: 10rem;
	// color: #6495ED;
	font-size: 5rem;

}
